<template>
  <v-container>
    <div class="submit-form mt-3 mx-auto">
      <!-- <p class="headline">Add Member</p> -->

      <v-row>
        <v-col class="text-center" cols="12" md="8">
          <v-row class="pa-5">
            <h2>ADD USER</h2>
          </v-row>
        </v-col>

        <v-col cols="12" md="4">
          <v-spacer />
          <v-btn
            to="/members"
            class="text-decoration-none"
            color="primary"
            dark
            block
            small
            >MEMBER LIST</v-btn
          >
        </v-col>
      </v-row>

      <!-- <div v-if="!submitted"> -->
      <div>
        <v-form ref="form" lazy-validation>
          <v-text-field
            v-model="member.username"
            :rules="[(v) => !!v || 'Name is required']"
            label="Name"
            required
          ></v-text-field>

          <v-text-field
            v-model="member.email"
            :rules="member.emailRules"
            label="Email"
            required
            type="email"
          ></v-text-field>

          <v-text-field
            v-model="member.password"
            :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
            :rules="[(v) => !!v || 'Password is required']"
            :type="show1 ? 'text' : 'password'"
            label="Password"
            required
            counter
            @click:append="show1 = !show1"
          ></v-text-field>
          <v-select
            v-model="member.roles"
            :items="access"
            item-text="name"
            item-value="roleCode"
            :menu-props="{ maxHeight: '400' }"
            label="USER LEVEL"
            hint="Set the user access"
            persistent-hint
          ></v-select>

          <v-select
            v-model="member.branch"
            :items="branch"
            item-text="name"
            item-value="roleCode"
            :menu-props="{ maxHeight: '400' }"
            label="BRANCH"
            hint="Set the user branch"
            persistent-hint
          ></v-select>

          <v-select
            v-model="member.dept"
            :items="dept"
            item-text="name"
            item-value="name"
            :menu-props="{ maxHeight: '400' }"
            label="DEPARTMENT"
            hint="Set the user department"
            persistent-hint
          ></v-select>

          <!-- <p>{{ member.roles }}</p> -->

          <v-checkbox v-model="member.active" :label="`Active`"></v-checkbox>
        </v-form>

        <v-btn
          :disabled="isAddButtonDisabled"
          color="primary"
          class="mt-3"
          @click="saveMember"
          >Submit</v-btn
        >
      </div>

      <!-- <div v-else>
        <v-card class="mx-auto">
          <v-card-title> Submitted successfully! </v-card-title>

          <v-card-subtitle> Click the button to add new User. </v-card-subtitle>

          <v-card-actions>
            <v-btn color="success" @click="newMember">Add</v-btn>
          </v-card-actions>
        </v-card>
      </div> -->
    </div>
    <!-- 
    <v-snackbar top v-model="snackbar" :timeout="timeout">
      {{ text }}

      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar> -->
  </v-container>
</template>

<script>
import MemberDataService from "../../services/MemberDataService";
import AccessDataService from "../../services/AccessDataService";
import BranchDataService from "../../services/BranchDataService";
import DeptDataService from "../../services/DeptDataService";

export default {
  name: "add-member",
  data() {
    return {
      access: [],
      branch: [],
      dept: [],

      show1: false,
      member: {
        id: null,
        username: "",
        email: "",
        emailRules: [
          (v) => !!v || "E-mail is required",
          (v) => /.+@.+/.test(v) || "E-mail must be valid",
        ],
        password: "",
        active: true,
        roles: "",
        branch: "",
        dept: "",
      },
      // submitted: false,
    };
  },

  computed: {
    isAddButtonDisabled() {
      return !(
        this.member.username &&
        this.member.email &&
        this.member.password &&
        this.member.roles &&
        this.member.branch &&
        this.member.dept
      );
      // means u have neither username and email and password
    },
  },

  methods: {
    retrieveAccess() {
      AccessDataService.getAll()
        .then((response) => {
          this.access = response.data.map(this.getDisplayAccess);
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    retrieveBranch() {
      BranchDataService.getAll()
        .then((response) => {
          this.branch = response.data;
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    retrieveDept() {
      DeptDataService.getAll()
        .then((response) => {
          this.dept = response.data;
          // console.log(response.data);
        })
        .catch((e) => {
          console.log(e);
        });
    },

    // refreshList() {
    //   this.retrieveAccess();
    //   this.retrieveBranch();
    // },

    saveMember() {
      var data = {
        username: this.member.username,
        email: this.member.email,
        password: this.member.password,
        active: this.member.active,
        roles: this.member.roles,
        branch: this.member.branch,
        dept: this.member.dept,
      };

      MemberDataService.create(data)
        .then((response) => {
          this.member.id = response.data.id;
          // console.log(response.data);
          // this.submitted = true;
          // this.snackbar = true;
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.$router.push({ name: "members" });
        })
        .catch((e) => {
          console.log(e);
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (e.response && e.response.data && e.response.data.message) ||
              e.message ||
              toString(),
          });
          // this.text =
          //   (e.response && e.response.data && e.response.data.message) ||
          //   e.message ||
          //   e.toString();
          // this.snackbar = true;
        });
    },

    // methods: {
    //   async saveVideo() {
    //     // let video = await this.$store.dispatch('editVideo', this.video);
    //     this.$store.dispatch('setSnackbar', { showing: true, text: `You have successfully edited your video, ${video.name}.`});
    //     // this.$router.push({name: 'admin-video-list'});
    //   },
    // },

    getDisplayAccess(role) {
      return {
        id: role.id,
        name: role.name,
        roleCode: [role.roleCode],
      };
    },

    // newMember() {
    //   this.submitted = false;
    //   this.member = {};
    // },
  },
  mounted() {
    this.retrieveAccess();
    this.retrieveBranch();
    this.retrieveDept();
  },
};
</script>
